import {Cookie,Local} from '@/../static/utils/storage'
import axios from 'axios'
import types from '../types'

const user ={
	state:{
		footerBarList: [{
			title: '',
			icon: 'home',
			path: '/home'
		}, {
			title: '',
			icon: 'video',
			path: '/video'
		}, {
			title: '',
			icon: 'findPerson',
			path: '/findPerson'
		}, {
			title: '',
			icon: 'minVideo',
			path: '/minVideo'
		}, {
			title: `${Cookie.get('user_userName')?'':''}`,
			icon: 'account',
			path: '/account'
		}],
		isLogin:Cookie.get('userName'),
		user:{
			userName:'',
			userInfo:[{title:'',num:20},{title:'',num:38},{title:'',num:89}],
			barList:[
			{icon:':icon-shoucang',title:'',color:'#F0C592'},
			{icon:':icon-lishi',title:'',color:'#F398B5'},
			{icon:':icon-yejian',title:'',color:'#8AA3F1'},
			{icon:':icon-rijian',title:'',color:'white'}
			],
			loginType:['shouji.png','weixin.png','qq.png','weibo.png'],
			showZhaiyao:false,
			showNotice:false,
			isClickGetNews:false
		},
		isNight:false,
		publishBar:[{title:'',icon:':icon-tuwen'},{title:'',icon:':icon-paishipin'},{title:'',icon:':icon-shipin'},{title:'',icon:':icon-tiwen'}],
		footBarSelectIndex:'/home',
		myGuanzhu:[],
		footBarLastClickIndex:'/home',
		refreshSearcHit:false
	},
	actions:{
		login({commit},params){
			return new Promise((resolve,reject)=>{
				if(params.userName=='12345678911'&params.pwd=='skywalker'){
					commit(types.ACCOUNT_LOGIN,params);
					resolve(true);
				}else{
					reject("12345678911 skywalker");
				}
			})
		},
		loginOut({commit},params){
			commit(types.ACCOUNT_LOGINOUT);
		}
	},
	mutations:{
		[types.ACCOUNT_LOGIN](state,data){
			state.user.userName=data.userName;
			state.isLogin=true;
			Cookie.set({user_userName:data.userName});
		},
		[types.ACCOUNT_LOGINOUT](state,data){
			Cookie.remove(['user_userName']);
			state.isLogin=false;
		}
	},
	namespaced: true
}

export default user


