import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/pages/layout/index.vue'
import Home from '@/pages/home/index'
import Video from '@/pages/video/index'
import FindPerson from '@/pages/findPerson/index'
import FenLei from '@/pages/findPerson/fenlei/index'
import MinVideo from '@/pages/minVideo/index'
import Account from '@/pages/account/index'
import Search from '@/pages/search/index'
import System from '@/pages/system/index'
import Unfinished from '@/pages/unfinished/index'


Vue.use(Router)
Router.prototype.animate = 0

const routes = [
{	
	path: '/',
	name: '',
	component: Layout,
	redirect: '/home',
	meta:{
		keepAlive:true
	},
	children: [{
		path: 'home',
		name: '',
		component: Home
	}]
}
,{	
	path: '/video',
	component: Layout,
	redirect: '/',
	meta:{
		keepAlive:true
	},
	children: [{
		path: '/',
		name: '',
		component: Video
	}]
},
{	
	path: '/findPerson',
	component: FindPerson,
	name: '',
	meta:{
		keepAlive:true
	}
},
{	
	path: '/minVideo',
	component: MinVideo,
	meta:{
		keepAlive:true
	}
},
{	
	path: '/account',
	component: Account,
	meta:{
		keepAlive:true
	},
	name: ''
},
{	
	path: '/search',
	component: Search,
	meta:{
		keepAlive:true
	},
	name:''
},
{
	path:'/system',
	component:System,
	name:'',
	meta:{
		slide:1,
		keepAlive:true
	}
},
{
	path:'/unfinished',
	component:Unfinished,
	name:'',
	meta:{
		slide:1,
		keepAlive:true
	}
},
{
	path:'/fenlei',
	component:FenLei,
	name:'',
	meta:{
		slide:1,
		keepAlive:true
	}
}
]

export default new Router({
	routes
})


