<template>
	<article class="systemWarp">
		<HeaderBar title=""></HeaderBar>
		<section class='toolList'>
			<div v-show="isLogin">
				<div class="line"></div>
				<toolBar @click.native="$router.push('/unfinished')">
					<span slot='left'></span>
					<mu-icon value=':icon-forword' slot='right'></mu-icon>
				</toolBar>
				<toolBar @click.native="$router.push('/unfinished')">
					<span slot='left'></span>
					<mu-icon value=':icon-forword' slot='right'></mu-icon>
				</toolBar>
				<toolBar @click.native="$router.push('/unfinished')">
					<span slot='left'></span>
					<mu-icon value=':icon-forword' slot='right'></mu-icon>
				</toolBar>
			</div>
			<div class="line"></div>
			<toolBar @click.native='clearStorage'>
				<span slot='left'></span>
				<span slot='right'>{{setInfo.storage}}</span>
			</toolBar>
			<toolBar @click.native='fontSizeShow=true'>
				<span slot='left'></span>
				<span slot='right'>{{setInfo.fontSize}}</span>
			</toolBar>
			<toolBar>
				<span slot='left'></span>
				<mu-switch slot='right' v-model="setInfo.showZhaiyao" @change='save' color='#4caf50'></mu-switch>
			</toolBar>
			<toolBar @click.native='picDownloadShow=true'>
				<span slot='left'>WiFi</span>
				<span slot='right'>{{setInfo.picDownload}}</span>
			</toolBar>
			<toolBar @click.native='noticeShow=true'>
				<span slot='left'>WiFi</span>
				<span slot='right'>{{setInfo.notice}}</span>
			</toolBar>
			<toolBar>
				<span slot='left'></span>
				<mu-switch slot='right' v-model="setInfo.showNotice" @change='save' color='#4caf50'></mu-switch>
			</toolBar>
			<toolBar @click.native="$router.push('/unfinished')">
				<span slot='left'></span>
				<mu-icon value=':icon-forword' slot='right'></mu-icon>
			</toolBar>
			<toolBar>
				<span slot='left'></span>
				<mu-switch slot='right' v-model="setInfo.isClickGetNews" @change='save' color='#4caf50'></mu-switch>
			</toolBar>
			<div class="line"></div>
			<toolBar @click.native="$router.push('/unfinished')">
				<span slot='left' ></span>
				<mu-icon value=':icon-forword' slot='right'></mu-icon>
			</toolBar>
			<div class="line"></div>
			<toolBar @click.native="$router.push('/unfinished')">
				<span slot='left'></span>
				<mu-icon value=':icon-forword' slot='right'></mu-icon>
			</toolBar>
			<toolBar @click.native="$router.push('/unfinished')">
				<span slot='left'></span>
				<span slot='right'>6.6.6</span>
			</toolBar>
			<toolBar @click.native="$router.push('/unfinished')">
				<span slot='left'></span>
				<mu-icon value=':icon-forword' slot='right'></mu-icon>
			</toolBar>

			<div v-show='isLogin'>
				<div class="line"></div>
				<div class="line"></div>
				<div class="line"></div>
				<div class='df-c login' @click='loginOut' >
					<span></span>
				</div>
			</div>

			<div class='copyright df-c'>
				<span>@skywalker</span>
			</div>
		</section>
		<div>
			<radioSetting v-show='fontSizeShow' :options='fontSizeOptions' title='' @selectChange='fontSizeSet' :value='setInfo.fontSize' @close='fontSizeShow=false'>			
			</radioSetting>
			<radioSetting v-show='picDownloadShow' :options='picDownloadOptions' title='WIFI' @selectChange='picDownloadSet' :value='setInfo.picDownload' @close='picDownloadShow=false'>			
			</radioSetting>
			<radioSetting v-show='noticeShow' :options='noticeOptions' title='WIFI' @selectChange='noticeSet' :value='setInfo.notice' @close='noticeShow=false'>			
			</radioSetting>
		</div>
	</article>
</template>

<style lang='less' scoped>

.systemWarp{
	.toolList {
		background-color: #fff;
		color: black;
		overflow-y: scroll;
		height: calc(100% - 0.5rem);
		.line {
			height: 0.1rem;
			background-color: #f7f3f3;
		}
		.login{
			color:red;
			font-size:0.16rem;
			height:0.3rem;
		}
		.copyright {
			height: 0.6rem;
			font-size: 0.12rem;
			background-color: #f7f3f3
		}
	}
}

</style>

<script>

import toolBar from '@/components/ToolBar/index'
import radioSetting from '@/components/radioSetting/index'
import {Local,Cookie} from '@/../static/utils/storage'
import {mapGetters} from 'vuex'

export default {
	components:{
		toolBar,
		radioSetting
	},
	data(){
		return {
			setInfo:{
				showZhaiyao:Local.get('system_showZhaiyao') || false,
				showNotice:Local.get('system_showNotice') || false,
				isClickGetNews:Local.get('system_isClickGetNews') || false,
				fontSize:Local.get('system_fontSize') || '',
				picDownload:Local.get('system_picDownload') || '',
				notice:Local.get('system_picDownload') || '',
				storage:""
			},
			fontSizeShow:false,
			picDownloadShow:false,
			noticeShow:false,
			fontSizeOptions : [{label: '',value: ''},{label: '',value: ''},{label: '',value: ''},{label: '',value: ''}],
			picDownloadOptions : [{label: '',value: ''},{label: '',value: ''},{label: '',value: ''}],
			noticeOptions : [{label: '',value: ''},{label: '',value: ''}]
		}
	},
	computed:{
		isLogin(){
			return this.$store.state.user.isLogin||Cookie.get('user_userName').length>0;	
		},
		...mapGetters(['user'])
	},
	methods:{
		clearStorage(){
			this.$confirm('','').then(({ result,value })=>{
				if(result){
					Local.clear();
					this.$set(this.setInfo,'storage',"");
				}
			}).catch(err=>{

			})
		},
		loginOut(){
			this.$confirm("",'',{
				okLabel:'',
				cancelLabel: ''
			}).then(({ result, value })=>{
				if(result){
					this.$store.dispatch('user/loginOut');
					this.$router.animate=2;
					this.$router.push('/account');
				}
			}).catch(err=>{

			})
		},
		save(){
			Local.set({
				system_showZhaiyao:this.setInfo.showZhaiyao,
				system_showNotice:this.setInfo.showNotice,
				system_isClickGetNews:this.setInfo.isClickGetNews
			})
			this.$set(this.setInfo,'storage',this.getStorage());
		},
		getStorage(){
			if(!Local.storage) {
				console.log('localStorage');
			}
			var size = 0;
			for(var item in Local.storage) {
				if(Local.storage.hasOwnProperty(item)) {
					size += Local.storage.getItem(item).length;
				}
			}
			return (size / 1024).toFixed(2) + 'KB';
		},
		fontSizeSet(val){
			Local.set({system_fontSize:val});
			this.$set(this.setInfo,'fontSize',val);
			this.fontSizeShow=false;
		},
		picDownloadSet(val){
			Local.set({system_picDownload:val});
			this.$set(this.setInfo,'picDownload',val);
			this.picDownloadShow=false;
		},
		noticeSet(val){
			Local.set({system_notice:val});
			this.$set(this.setInfo,'notice',val);
			this.noticeShow=false;
		}
	},
	mounted(){
		this.$set(this.setInfo,'storage',this.getStorage());
	}
}
</script>


