<template>
    <div class="login">
        <div class='close' @click="$emit('closeLogin')">
            <mu-icon value=':icon-close'></mu-icon>
        </div>
        <div class='title df-sa'></div>
        <div class='df-sa'>
            <div class='shuru df-sb'>
                <input type="number" class='haoma' placeholder="12345678911" v-model='userName'>
                <div class='tips'></div>
            </div>
        </div>
        <div class='df-sa'>
            <div class='yanzhengma shuru df-sb'>
                <input type="text" placeholder=""  v-model='pwd'>
            </div>
        </div>
        <div class="df-sa">
            <div class="loginSubmit shuru df-c" @click='login'>
                <span></span>
            </div>
        </div>

        <div class='xieyi df-c'>
            <span></span><span></span>
        </div>
        <div class='df-c'>
            <mu-button flat color="primary"></mu-button>
            <mu-button flat color="primary"></mu-button>
        </div>
        <div class="bottom" v-show="hidshow">
            <div class="df-sa">
                <mu-avatar size='50' color="white">
                    <img src="~@/../static/img/weixin.png">
                </mu-avatar>
                <mu-avatar size='50' color="white">
                    <img src="~@/../static/img/qq.png">
                </mu-avatar>
                <mu-avatar size='50' color="white">
                    <img src="~@/../static/img/weibo.png">
                </mu-avatar>
            </div>
        </div>
    </div>
</template>

<style lang='less' scoped>
.login {
    border-radius: 10px;
    background-color: #fafafa;
    .close {
        position: absolute;
        right: 20px;
        top: 10px;
        font-size: 0.2rem;
    }
    .title {
        font-size: 20px;
        height: 1rem;
    }
    .shuru {
        width: 90%;
        height: 0.5rem;
        border-radius: 10px;
        border: 1px solid #ccc;
        margin-top: 0.2rem;
        background-color: white;
        position: relative;
        input {
            border: none;
            font-size: 0.16rem;
            line-height: 0.4rem;
            height: 0.4rem;
            text-indent: 0.3rem;
            outline: none;
        }
        .haoma{
            width: calc(100% - 1rem);
            border-right: 1px solid #ccc;
        }

    }
    .loginSubmit {
        background-color: #F9ABAB;
        color: white;
        font-size: 0.18rem;
    }
    .xieyi {
        font-size: 0.12rem;
        margin-top: 0.15rem;
        span {
            color: #6680A6;
        }
    }
    .yanzhengma{
        input {
            width: 100%;
        }
    }
    .tips {
        font-size: 0.16rem;
        position: absolute;
        right: 0.1rem;
    }
    .bottom {
        position: absolute;
        bottom: 0.3rem;
        width: 100%;
        img {
            width: 70%;
            height: 70%;
        }
    }
}

</style>

<script>
export default {
    data(){
        return {
            userName:'',
            pwd:'',
            docmHeight: document.documentElement.clientHeight,  
            showHeight: document.documentElement.clientHeight, 
            hidshow:true 
        }
    },
    watch:{
        showHeight() {
            if(this.docmHeight > this.showHeight){
                this.hidshow=false
            }else{
                this.hidshow=true
            }
        }
    },
    methods:{
       async login(){
        try{
         await this.$store.dispatch('user/login',{userName:this.userName,pwd:this.pwd}).then(res=>{
             this.$set(this.$store.state.user.footerBarList, 4, {title: '', icon: 'account', path: '/account'});
             this.$emit('closeLogin');
         }).catch(err=>{
            this.$alert(err, '', { okLabel: ''});
        });
     }
     catch(err){

     }       
 }
},
mounted(){
    window.onresize = ()=>{
        return(()=>{
            this.showHeight = document.body.clientHeight;
        })()
    }
}
}
</script>


